.difficulty-selection {
    text-align: center;
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
}

.difficulty-selection h1 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #333;
}

.difficulty-selection p {
    font-size: 18px;
    margin-bottom: 20px;
    color: #555;
}

.difficulty-selection h2 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #444;
}

.difficulty-selection input[type="range"] {
    width: 80%;
    margin: 10px 0;
    cursor: pointer;
    -webkit-appearance: none; /* 기본 브라우저 스타일 제거 */
    appearance: none;
    background: transparent; /* 기본 배경 제거 */
}

.difficulty-selection input[type="range"]::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px; /* 높이 조정 */
    background: #a8e6cf; /* 연한 연두색으로 변경 */
    border-radius: 5px;
}

.difficulty-selection input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    margin-top: -7px; /* thumb를 가운데 정렬 */
    border-radius: 50%;
    background-color: #4CAF50; /* 동그라미 색상 설정 */
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.difficulty-selection input[type="range"]::-moz-range-track {
    width: 100%;
    height: 6px;
    background: #a8e6cf; /* 연한 연두색으로 변경 */
    border-radius: 5px;
}

.difficulty-selection input[type="range"]::-moz-range-thumb {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #4CAF50;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.version-info {
    font-size: 14px;
    color: #777;
    margin-bottom: 10px;
}

.difficulty-selection button {
    padding: 12px 24px; /* 버튼 크기 조정 */
    font-size: 16px;
    cursor: pointer;
    background-color: #4CAF50; /* 버튼 배경색 */
    color: white; /* 텍스트 색상 */
    border: none;
    border-radius: 8px; /* 둥근 모서리 */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* 버튼 그림자 */
    transition: background-color 0.3s ease, transform 0.1s ease;
}

.difficulty-selection button:hover {
    background-color: #45a049; /* 호버 시 배경색 */
}

.difficulty-selection button:active {
    transform: scale(0.98); /* 클릭 시 살짝 작아지는 효과 */
}
